.save-btn {
  padding: 0;
  margin: 0;
  background: #000000;
  border-radius: 2px;
  color: #fff;
  border: none;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.22;
  text-align: center;
  min-height: 50px;
  outline: none;
  cursor: pointer;
}

.save-btn:hover {
  opacity: 0.8;
}
